<template>
    <div class="category">
        <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item to="/category   ">分类管理</el-breadcrumb-item>
        <el-breadcrumb-item>分类列表</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 添加分类 -->
        <el-button type="success" class="showT" @click="showAdd" plain>添加分类</el-button>
        <!-- 分类列表表格 -->
        <el-table
            :data="categoryList"
            style="width: 100%">
            <el-table-column
                prop="createTime"
                label="创建日期"
                width="180">
            </el-table-column>
            <el-table-column
                prop="name"
                label="分类名称"
            >
            </el-table-column>
            <el-table-column label="操作">
            <template slot-scope="scope">
                <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                plain
                circle
                @click="Dele(scope)"
                ></el-button>
            </template>
            </el-table-column>
        </el-table>
        <!-- 添加的弹窗 -->
        <el-dialog
          title="添加分类"
          :visible.sync="addDialogVisible"
          width="40%">
          <!-- 表单组件 -->
          <el-input class="elpt" v-model="addcategory"></el-input>
          <span slot="footer" class="dialog-footer">
              <el-button @click="addDialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="addCategory">确 定</el-button>
          </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
  data () {
    return {
      addDialogVisible: false,
      categoryList: [],
      addcategory: ''
    }
  },
  created () {
    this.getList()
  },
  methods: {
    open () {
      this.$alert('未授权，请登录！', '温馨提示', {
        confirmButtonText: '确定',
        callback: action => {
          this.$router.push('/login')
        }
      })
    },
    showAdd () {
      this.addDialogVisible = true
    },
    async getList () {
      let res = await this.axios('http://192.168.1.131:8080/ptype/findList')
      if (res.code === '200') {
        this.categoryList = res.data
        console.log(res)
      } else if (res.code === '401') {
        this.open()
      }
    },
    async addCategory () {
      let res = await this.axios.post('http://192.168.1.131:8080/ptype/save?name=' + this.addcategory)
      if (res.code === '200') {
        this.addDialogVisible = false
        this.addcategory = ''
        this.getList()
      }
    },
    async Dele (scope) {
      let res = await this.axios(`http://192.168.1.131:8080/ptype/delete?typeid=${scope.row.id}`)
      if (res.code === '200') {
        this.getList()
      }
    }
  }
}
</script>
<style lang="less" scoped>
.showT{
    margin-bottom: 10px;
}
.elpt{
    width: 500rpx;
}
</style>



