<template>
  <div class="user">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="姓名:">
        <el-input v-model="form.nickName"></el-input>
      </el-form-item>
      <el-form-item label="手机号:">
        <el-input v-model="form.mobile"></el-input>
      </el-form-item>
       <el-button @click="search" type="primary" icon="el-icon-search" plain round>搜索</el-button>
  </el-form>
   <el-row>
      <el-table
        :data="userList"
        style="width: 100%">
        <el-table-column type="index" :index="indexMethod"></el-table-column>
        <el-table-column
          prop="username"
          label="用户名"
          :span="4">
        </el-table-column>
        <el-table-column
          prop="score"
          label="积分"
          :span="4"
        >
        </el-table-column>
        <el-table-column
          prop="userType"
          label="用户类型"
          :span="4"
        >
        <template slot-scope="scope">
          <span v-if="scope.row.userType === 0">正常用户</span>
          <span v-else-if="scope.row.userType === 100">管理员</span>
          <span v-else>超级管理员</span>
        </template>
        </el-table-column>
         <el-table-column label="积分记录" :span="4">
        <template slot-scope="scope">
          <el-button @click="showCheck(scope.row)" type="primary" size="mini" plain round>查看</el-button>
          <el-button @click="showEdit(scope.row)" type="primary"  size="mini" round>修改</el-button>
        </template>
      </el-table-column>
      </el-table>
      </el-row>
      <!-- 分页 -->
       <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="listPage.currentPage"
        :page-size="listPage.pageSize"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="listPage.total">
       </el-pagination>
      <!-- 查看的对话框 -->
    <el-dialog
      title="用户积分记录"
      :visible.sync="checkDialogVisible"
      width="40%">
      <!-- 表单组件 -->
      <el-form ref="checkForm" status-icon :model="checkForm" label-width="80px">
        <el-form-item label="用户名:" class="fl">
          <el-tag type="info">{{checkForm.username}}</el-tag>
        </el-form-item>
         <el-form-item label="积分:">
          <el-tag type="info">{{checkForm.score}}</el-tag>
        </el-form-item>
      </el-form>
      <!-- 表格 -->
      <el-table :data="scoreList">
        <el-table-column type="index" :index="indexMethod1"></el-table-column>
        <el-table-column prop="operationType" label="操作类型" width="150">
           <template slot-scope="scope">
          <span v-if="scope.row.operationType === 0">正常用户</span>
          <span v-else-if="scope.row.operationType === 100">管理员</span>
          <span v-else>超级管理员</span>
        </template>
        </el-table-column>
        <el-table-column prop="updateTime" label="积分调整时间" width="200"></el-table-column>
        <el-table-column prop="score" label="积分数"></el-table-column>
      </el-table>
       <!-- 表格的分页 -->
       <el-pagination
        background
        @size-change="handleSizeChange1"
        @current-change="handleCurrentChange1"
        :current-page="scorePage.currentPage"
        :page-size="scorePage.pageSize"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="scorePage.total">
       </el-pagination>
      <span slot="footer" class="dialog-footer">
        <el-button @click="checkDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="checkDialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改的对话框 -->
    <el-dialog
      title="修改用户积分"
      :visible.sync="editDialogVisible"
      width="40%">
      <!-- 表单组件 -->
      <el-form ref="editForm" status-icon :model="editForm" label-width="80px">
        <el-form-item label="用户名:" class="fl">
          <el-tag type="info">{{editForm.username}}</el-tag>
        </el-form-item>
         <el-form-item label="手机号:">
          <el-tag type="info">{{editForm.mobile}}</el-tag>
        </el-form-item>
        <el-form-item label="用户类型:">
          <!-- 选择器 -->
            <el-select v-model="editForm.userType" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="积分:">
          <el-input v-model="editForm.score" placeholder="请输入积分"></el-input>
        </el-form-item>
        <el-form-item label="修改原因:">
          <el-input v-model="editForm.scoreRemark" placeholder="请输入修改原因"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateUser">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 分页列表数据渲染
      listPage: {
        pageSize: 10,
        currentPage: 1,
        total: 0
      },
      userList: [],
      form: {
        nickName: '',
        mobile: ''
      },
      //  查看用户积分记录
      checkDialogVisible: false,
      checkForm: {
        username: '',
        score: '',
        id: ''
      },
      scoreList: [],
      //  表格的分页数据
      scorePage: {
        pageSize: 10,
        currentPage: 1,
        total: 0
      },
      //  修改用户积分
      editDialogVisible: false,
      editForm: {
        username: '',
        mobile: '',
        userType: 0,
        score: 0,
        scoreRemark: '',
        id: ''
      },
      //  选择器
      options: [
        {
          value: 0,
          label: '正常用户'
        },
        {
          value: 100,
          label: '管理员'
        }
      ],
      value: ''
    }
  },
  created () {
    this.getuserList()
    this.getuserPage()
  },
  methods: {
    //  获取用户列表
    async getuserList () {
      let res = await this.axios('user/list')
      if (res.code === '200') {
        this.userList = res.data
        console.log(res)
      }
    },
    async getuserPage () {
      let res = await this.axios({
        method: 'get',
        url: 'user/findPage',
        params: {
          pageSize: this.listPage.pageSize,
          pageNo: this.listPage.currentPage
        }
      })
      if (res.code === '200') {
        this.userList = res.data.list
        this.listPage.total = res.data.total
      }
    },
    handleSizeChange (val) {
      this.listPage.pageSize = val
      this.getuserPage()
    },
    handleCurrentChange (val) {
      this.listPage.currentPage = val
      this.getuserPage()
    },
    indexMethod (index) {
      return index + 1 + (this.listPage.currentPage - 1) * this.listPage.pageSize
    },
    async search () {
      let res = await this.axios(`user/findPage?nickName=${this.form.nickName}&mobile=${this.form.mobile}`)
      if (res.code === '200') {
        this.userList = res.data.list
      }
    },
    //  获取积分列表
    async getscoreList () {
      let res = await this.axios(`score/findList?userid=${this.checkForm.id}`)
      if (res.code === '200') {
        this.scoreList = res.data
        this.scorePage.total = res.data.total
      }
    },
    async getscorePage () {
      let res = await this.axios({
        method: 'get',
        url: `score/findPage?userid=${this.checkForm.id}`,
        params: {
          pageSize: this.scorePage.pageSize,
          pageNo: this.scorePage.currentPage
        }
      })
      if (res.code === '200') {
        this.scoreList = res.data.list
        this.scorePage.total = res.data.total
      }
    },
    handleSizeChange1 (val) {
      this.scorePage.pageSize = val
      this.getscorePage()
    },
    handleCurrentChange1 (val) {
      this.scorePage.currentPage = val
      this.getscorePage()
    },
    indexMethod1 (index) {
      return index + 1 + (this.scorePage.currentPage - 1) * this.scorePage.pageSize
    },
    showCheck (user) {
      this.checkDialogVisible = true
      this.checkForm.id = user.id
      this.checkForm.username = user.username
      this.checkForm.score = user.score
      this.getscoreList()
      this.getscorePage()
    },
    showEdit (user) {
      this.editDialogVisible = true
      this.editForm.username = user.username
      this.editForm.mobile = user.mobile
      this.editForm.userType = user.userType
      this.editForm.score = user.score
      this.editForm.scoreRemark = user.scoreRemark
      this.editForm.id = user.id
    },
    async updateUser () {
      let res = await this.axios({
        method: 'post',
        url: 'user/update',
        data: this.editForm
      })
      if (res.code === '200') {
        this.$refs.editForm.resetFields()
        this.editDialogVisible = false
        this.getuserList()
        this.$message.success('修改用户信息完成')
      } else {
        this.$message.error('修改信息失败')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.user{
  .el-form{
    .el-input{
    width: 300px;
  }
  .el-button{
    margin-left:400px;
    margin-bottom: 15px;
  }
  }
  .el-dialog{
    overflow: hidden;
    .fl{
      float:left;
    }
  }
}
</style>



