<template>
    <div class="product">
        <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item to="/product">产品管理</el-breadcrumb-item>
        <el-breadcrumb-item>产品列表</el-breadcrumb-item>
        </el-breadcrumb>
         <!-- 搜索框 -->
      <!-- <el-input placeholder="请输入商品ID" v-model="productId" class="input-with-select">
        <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
      </el-input> -->
      <el-select v-model="productTypeId" @change="update" placeholder="选择分类">
        <el-option
          v-for="item in categoryList"
          :key="item.value"
          :label="item.name"
          :value="item.id">
        </el-option>
      </el-select>
      <el-button type="success" plain @click="showAdd">添加商品</el-button>
        <!-- 商品列表表格 -->
      <el-table
        :data="productList"
        style="width: 100%">
        <el-table-column
          prop="name"
          label="商品名称"
          width="300">
        </el-table-column>
        <el-table-column
          prop="price"
          label="价格"
          width="500"
        >
        </el-table-column>
         <el-table-column width="300"  label="操作">
        <template slot-scope="scope">
          <el-button @click="showEdit(scope.row)" type="primary" icon="el-icon-edit" size="mini" plain circle></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            plain
            circle
            @click="delProduct(scope.row.id)"
          ></el-button>
        </template>
      </el-table-column>
      </el-table>
      <!-- 添加的对话框 -->
    <el-dialog
      title="添加用户"
      :visible.sync="addDialogVisible"
      width="40%">
      <!-- 表单组件 -->
      <el-form ref="addForm" status-icon :model="addForm" label-width="80px" class="addt">
        <el-form-item label="商品名称" prop="name">
          <el-input v-model="addForm.name" placeholder="请输入商品名称"></el-input>
        </el-form-item>
        <el-form-item label="商品分类">
          <el-select v-model="addForm.productTypeId" placeholder="选择分类">
            <el-option
              v-for="item in categoryList"
              :key="item.value"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="价格" prop="price">
          <el-input v-model="addForm.price" placeholder="请输入价格"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addProduct">确 定</el-button>
      </span>
    </el-dialog>
        <!-- 修改的对话框 -->
     <el-dialog
      title="修改商品"
      :visible.sync="editDialogVisible"
      width="40%">
      <!-- 表单组件 -->
      <quill-editor v-model="content"
                ref="myQuillEditor"
                @blur="onEditorBlur($event)"
                @focus="onEditorFocus($event)">
      </quill-editor>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editDialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
    </div>
</template>
<script>
export default {
  data () {
    return {
      categoryList: [],
      productTypeId: '111',
      content: '',
      defaultMsg: '这里是UE测试',
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350
      },
      ue1: 'user1',
      productList: [],
      productId: '',
      addDialogVisible: false,
      addForm: {
        name: '',
        productTypeId: '',
        price: ''
      },
      editDialogVisible: false,
      editForm: {
        name: '',
        price: '',
        id: ''
      }
    }
  },
  created () {
    this.getproductList()
    this.getList()
  },
  methods: {
    async getList () {
      let res = await this.axios('http://192.168.1.131:8080/ptype/findList')
      if (res.code === '200') {
        this.categoryList = res.data
        // console.log(res)
      } else if (res.code === '401') {
        this.open()
      }
    },
    open () {
      this.$alert('未授权，请登录！', '温馨提示', {
        confirmButtonText: '确定',
        callback: action => {
          this.$router.push('/login')
        }
      })
    },
    async getproductList () {
      let res = await this.axios('http://192.168.1.131:8080/product/findList?productTypeId=' + this.productTypeId)
      if (res.code === '200') {
        this.productList = res.data
        // this.total = res.data.total
        console.log(res.data)
      } else if (res.code === '401') {
        this.open()
      }
    },
    async search () {
      let res = await this.axios(`http://192.168.0.109:8080/product/get?productId=${this.productId}`)
      if (res.code === '200') {
        this.productList = []
        this.productList.push(res.data)
      }
    },
    showAdd () {
      this.addDialogVisible = true
    },
    async addProduct () {
      let res = await this.axios({
        method: 'post',
        url: `http://192.168.1.131:8080/product/save`,
        data: this.addForm
      })
      if (res.code === '200') {
        this.$refs.addForm.resetFields()
        this.addDialogVisible = false
        this.getproductList()
        this.$message.success(res.message)
        console.log(res.data)
      } else {
        this.$message.error('商品添加失败')
        console.log(this.addForm)
      }
    },
    showEdit (product) {
      this.editDialogVisible = true
      this.editForm.id = product.id
      this.editForm.name = product.name
      this.editForm.price = product.price
    },
    async delProduct (id) {
      try {
        await this.$confirm('你确定要删除该角色吗', '温馨提示', {type: 'warning'})
        // console.log(res)
        let res = await this.axios(`http://192.168.1.131:8080/product/delete?productid=${id}`)
        if (res.code === '200') {
          this.getproductList()
          this.$message.success('删除成功')
        }
      } catch (e) {
        this.$message.info('取消删除')
      }
    },
    async update () {
      this.getproductList()
    }
  }
}
</script>

<style lang="less" socped>
.el-input{
  width: 400px;
  margin-bottom: 10px;
}
</style>



