<template>
    <div class="login">
      <div class="loginP">
        <div class="tab">
          <el-button type="info" @click="login1(1)">朴存员工登录</el-button>
          <el-button type="info" @click="login1(2)">钉钉扫码登录</el-button>
          <el-button type="info" @click="login1(3)">钉钉授权登录</el-button>
        </div>
        <div class="loginContent" v-if="temp === 1">
          <el-form ref="form" :model="form" :rules="rules" label-width="80px" status-icon>
            <el-form-item label="用户名" prop="username">
              <el-input v-model="form.username" placeholder="请输入用户名"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input
                v-model="form.password"
                placeholder="请输入密码"
                type="password"
                @keyup.enter.native="login"
              ></el-input>
            </el-form-item>
            <el-form-item>
                <el-form-item>
                  <el-checkbox-group v-model="form.rememberMe">
                    <el-checkbox label="记住密码" name="rememberMe"></el-checkbox>
                  </el-checkbox-group>
                </el-form-item>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="login">登录</el-button>
              <el-button @click="reset">重置</el-button>
            </el-form-item>
          </el-form>
        </div>
        <div class="loginContent" v-if="temp === 2">
          <iframe class="DDT" src="https://oapi.dingtalk.com/connect/oauth2/sns_authorize?appid=dingoadqx4yx1dt8vx9ctv&response_type=code&scope=snsapi_login&state=STATE&redirect_uri=http://192.168.1.171/login.html" marginheight="-30px"  target="_parent" frameborder="0" width="400px" scrolling="no" height="340px"></iframe>
        </div>
        <div class="loginContent" v-show="temp === 3">
          <iframe class="ImgCode" width="365px" scrolling="no" height="400px" src="http://192.168.1.171/111.html" frameborder="0"></iframe>
          <!-- <div id="login_container"></div> -->
        </div>
      </div>
    </div>
</template>
<script>
// import{ DDLogin} from '//g.alicdn.com/dingding/dinglogin/0.0.5/ddLogin.js'
export default {
  data () {
    return {
      temp: 1,
      form: {
        username: '',
        password: ''
        // rememberMe: false
      },
      // 表单的校验规则
      rules: {
        username: [
          { required: true, message: '用户名不能为空', trigger: 'change' },
          { min: 3, max: 12, message: '长度在 3 到 12 个字符', trigger: 'change' }
        ],
        password: [
          { required: true, message: '密码不能为空', trigger: 'change' },
          {
            min: 6,
            max: 12,
            message: '长度在 6 到 12 个字符',
            trigger: 'change'
          }
        ]
      }
    }
  },
  methods: {
    login1 (t) {
      // console.log(t)
      this.temp = t
      // console.log(this.temp)
    },
    reset () {
      this.$refs.form.resetFields()
    },
    async login () {
      try {
        await this.$refs.form.validate()
        // 发送ajax请求
        let res = await this.axios({
          method: 'get',
          url: 'http://192.168.1.131:8080/token',
          params: this.form
        })
        console.log(res)
        if (res.code === '200') {
          this.$message.success('登录成功')
          localStorage.setItem('token', res.data)
          localStorage.setItem('userInfo', JSON.stringify(this.form))
          this.$router.push('/home')
        } else {
          this.$message.error('登录失败')
        }
      } catch (e) {
        return false
      }
    }
  },
  created () {
    // const oScript = document.createElement('script')
    // oScript.type = 'text/javascript'
    // oScript.src = '//g.alicdn.com/dingding/dinglogin/0.0.5/ddLogin.js'
    // document.body.appendChild(oScript)
  },
  mounted () {
    // setTimeout (()=>{
    // const oScript = document.createElement('script')
    // oScript.type = 'text/javascript'
    // oScript.src = '//g.alicdn.com/dingding/dinglogin/0.0.5/ddLogin.js'
    // document.body.appendChild(oScript)
    // setTimeout(() => {
    //   var url = encodeURIComponent('http://192.168.1.171/login.html')
    //   var goto = encodeURIComponent('https://oapi.dingtalk.com/connect/oauth2/sns_authorize?appid=dingoadqx4yx1dt8vx9ctv&response_type=code&scope=snsapi_login&state=STATE&redirect_uri='+url)
    //   var obj = DDLogin({
    //   id:"login_container",//这里需要你在自己的页面定义一个HTML标签并设置id，例如<div id="login_container"></div>或<span id="login_container"></span>
    //   goto: goto, //请参考注释里的方式
    //   style: "border:none;background-color:#fff;",
    //   width : "365",
    //   height: "400"
    // })
    // const self = this
    // var hanndleMessage = function (event) {
    //   var origin = event.origin
    //   // console.log("origin", event.origin)
    //   if( origin == "https://login.dingtalk.com" ) { //判断是否来自ddLogin扫码事件。
    //     var loginTmpCode = event.data //拿到loginTmpCode后就可以在这里构造跳转链接进行跳转了
    //     console.log("loginTmpCode", loginTmpCode)
    //     // self.axios(`http://192.168.1.143:8080/dingdingToken?code=${loginTmpCode}`)
    //     // .then(res=>{
    //     //   console.log(res)
    //     // })
    //   }
    // }
    // if (typeof window.addEventListener != 'undefined') {
    //     window.addEventListener('message', hanndleMessage, false);
    // } else if (typeof window.attachEvent != 'undefined') {
    //     window.attachEvent('onmessage', hanndleMessage);
    // }
    //   },1000)
    if (localStorage.getItem('userInfo') && JSON.parse(localStorage.getItem('userInfo')).rememberMe === true) {
      let user = JSON.parse(localStorage.getItem('userInfo'))
      this.form.username = user.username
      this.form.password = user.password
      this.form.rememberMe = user.rememberMe
    }
  }
}
</script>

<style lang="less" scoped>
.login {
  width: 100%;
  height: 100%;
  background-color: #2d434c;
  overflow: hidden;
  .loginP{
    width: 480px;
    height: 400px;
    background-color: #fff;
    margin: 200px auto;
    // padding: 75px 40px 15px;
    // padding: 75px 40px 15px;
    border-radius: 10px;
    border: 1px solid #ccc;
    // a{
    //   display: block;
    //   float: left;
    //   width: 30%;
    //   height: 40px;
    //   background-color: #ccc;
    //   color:red;
    //   border-left: 1px solid red;
    // }
    .DDT{
      margin: 0 auto;
      display: block;
      border-radius: 2px;
    }
    .tab{
      // margin-top:20px;
      width: 388px;
      margin: 0 auto;
      .el-button{
        margin-left: 0;
      }
    }
    .ImgCode{
      display: block;
      margin: 0 auto;
    }
  }
  .el-form {
    width: 400px;
    background-color: #fff;
    // margin: 200px auto;
    padding: 75px 40px 15px;
    position: relative;
    border-radius: 10px;
    .el-button ~ .el-button {
      margin-left: 80px;
    }
    .el-input{
      width: 320px;
    }
  }
}
</style>



