<template>
  <div class="order">
    <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item to="/order">订单管理</el-breadcrumb-item>
        <el-breadcrumb-item>订单列表</el-breadcrumb-item>
        </el-breadcrumb>
         <!-- 搜索框 -->
      <el-input placeholder="请输入订单ID" v-model="orderid" class="input-with-select">
        <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
      </el-input>
      <!-- 订单列表表格 -->
       <el-table
        :data="orderList"
        style="width: 100%">
        <el-table-column
          prop="orderNo"
          label="订单号">
        </el-table-column>
        <el-table-column
          prop="totalPrice"
          label="价格"
        >
        </el-table-column>
        <el-table-column label="操作" width="220" class="operation">
          <template slot-scope="scope">
            <el-button @click="Tong(scope.row)" type="primary" v-if='scope.row.orderStatus === 0' size="mini" plain circle>通过</el-button>
            <el-button
              v-if='scope.row.orderStatus === 0'
              type="danger"
              size="mini"
              plain
              circle
              @click="BoEdite(scope.row.id)"
            >驳回</el-button>
            <el-popover
              placement="right"
              width="400"
              trigger="click">
              <p style="margin-bottom:10px">订单号：{{details.orderNo}}</p>
              <p style="margin-bottom:10px">订单创建时间:{{details.createTime}}</p>
              <p style="margin-bottom:10px">收货地址：{{details.address}}</p>
              <el-table :data="productObj" style="margin-bottom:10px">
                <el-table-column  property="productName" label="产品名称"></el-table-column>
                <el-table-column  property="amount" label="产品数量"></el-table-column>
                <el-table-column  property="price" label="购买单价"></el-table-column>
              </el-table>
              <el-button type="danger" size="mini" round plain v-if='scope.row.orderStatus === 0'  @click="Bo(scope.row.id)" >驳回</el-button>
              <el-button type="success" size="mini" round plain v-if='scope.row.orderStatus === 0'  @click="Tong(scope.row)" >同意</el-button>
              <el-button slot="reference" plain type="success" @click="wacthX(scope.row)" round size="mini" style="float:right">查看详情</el-button>
            </el-popover>
          </template>
        </el-table-column>
      </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      details: {
      },
      productObj: [],
      orderList: [],
      orderid: '',
      editDialogVisible: false,
      editForm: {
        orderNo: '',
        totalPrice: '',
        id: ''
      }
    }
  },
  created () {
    this.getorderList()
  },
  methods: {
    async getorderList () {
      let res = await this.axios('http://192.168.1.131:8080/order/findList')
      if (res.code === '200') {
        this.orderList = res.data
        console.log(res.data)
      }
    },
    async search () {
      let res = await this.axios(`http://192.168.0.109:8080/order/get?orderid=${this.orderid}`)
      if (res.code === '200') {
        this.orderList = []
        this.orderList.push(res.data)
      }
    },
    showEdit (order) {
      this.editDialogVisible = true
      this.editForm.id = order.id
      this.editForm.orderNo = order.orderNo
      this.editForm.totalPrice = order.totalPrice
    },
    async Bo (id, remark) {
      let res = await this.axios({
        method: 'post',
        url: 'http://192.168.1.131:8080/order/invalid',
        data: {
          id, remark
        }
      })
      if (res.code === '200') {
        this.getorderList()
        this.$message({
          type: 'success',
          message: '驳回成功'
        })
      }
    },
    async Tong (row) {
      let res = await this.axios(`http://192.168.1.131:8080/order/pass?orderId=${row.id}`)
      if (res.code === '200') {
        this.getorderList()
      }
    },
    async wacthX (row) {
      this.details = row
      let res = await this.axios(`http://192.168.1.131:8080/order/item/findPage?orderid=${row.id}`)
      if (res.code === '200') {
        this.productObj = res.data.list
      }
    },
    BoEdite (id) {
      this.$prompt('请输入驳回原因', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
        // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        // inputErrorMessage: '邮箱格式不正确'
      }).then(({ value }) => {
        this.Bo(id, value)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消输入'
        })
      })
    }
  }
}
</script>

<style lang="less" socped>
.el-input{
  width: 400px;
  margin-bottom: 10px;
}
</style>



